	module.exports = {"data":{"markdownRemark":{"html":"<h1>Euler’s Method</h1>\n<p>The Euler’s method is a first-order numerical procedure for solving ordinary differential equations (ODE) with a given initial value.</p>\n<h2>The General Initial Value Problem</h2>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn006.png\"></p>\n<h2>Methodology</h2>\n<p>Euler’s method uses the simple formula,</p>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn3.png\"></p>\n<p>to construct the tangent at the point <code>x</code> and obtain the value of <code>y(x+h)</code>, whose slope is, </p>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn008.png\"></p>\n<img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/Euler.png\" width=\"600\">\n<p>In Euler’s method, you can approximate the curve of the solution by the tangent in each interval (that is, by a sequence of short line segments), at steps of <code>h</code>.</p>\n<p><i>In general</i>, if you use small step size, the accuracy of approximation increases.</p>\n<h2>General Formula</h2>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn7.png\"></p>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn_new_2.png\"></p>\n<h2>Functional value at any point <code>b</code>, given by <code>y(b)</code></h2>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn6.png\"></p>\n<p>where,</p>\n<ul>\n<li><b>n</b> = number of steps</li>\n<li><b>h</b> = interval width (size of each step)</li>\n</ul>\n<h3>Pseudocode</h3>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn_new_1.png\"></p>\n<h2>Example</h2>\n<p>Find <code>y(1)</code>, given</p>\n<p>     <img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/eqn007.png\"></p>\n<p>Solving analytically, the solution is <i><b>y = e<sup>x</sup></b></i> and <code>y(1)</code>= <code>2.71828</code>. (Note: This analytic solution is just for comparing the accuracy.)</p>\n<p>Using Euler’s method, considering <code>h</code> = <code>0.2</code>, <code>0.1</code>, <code>0.01</code>, you can see the results in the diagram below.</p>\n<p><img src=\"https://raw.githubusercontent.com/pranabendra/articles/master/Euler-method/images/comparison.png\"></p>\n<p>When <code>h</code> = <code>0.2</code>, <code>y(1)</code> = <code>2.48832</code>  (error = 8.46 %)</p>\n<p>When <code>h</code> = <code>0.1</code>, <code>y(1)</code> = <code>2.59374</code>  (error = 4.58 %)</p>\n<p>When <code>h</code> = <code>0.01</code>, <code>y(1)</code> = <code>2.70481</code>  (error = 0.50 %)</p>\n<p>You can notice, how accuracy improves when steps are small.</p>\n<h2>More Information:</h2>\n<ol>\n<li><a href='http://calculuslab.deltacollege.edu/ODE/7-C-1/7-C-1-h-c.html' target='_blank' rel='nofollow'>Numerical Methods for Solving Differential Equations</a></li>\n<li><a href='https://en.wikipedia.org/wiki/Euler_method' target='_blank' rel='nofollow'>Euler’s method</a></li>\n</ol>","fields":{"slug":"/mathematics/differential-equations/eulers-method/"},"frontmatter":{"title":"Euler's Method"}}},"pathContext":{"slug":"/mathematics/differential-equations/eulers-method/"}}

